.carousel-header h2 {
    font-size: 2.5em;
    margin-bottom: 10px;
    color: rgb(0, 0, 0);
}

.carousel-header p {
    font-size: 1.2em;
    color: #050505;
    margin-bottom: 40px;
}

.stepper-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
   image-orientation: inherit;
}

.steps {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 20px;
    position: relative;
}

.step {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 10px;
    padding: 20px;
    text-align: center;
    transition: transform 0.3s ease-in-out;
    width: calc(9% - 20px);
    cursor: pointer;
    position: relative;
}

.step:hover {
    transform: scale(1.05);
}

.step.visited {
    background-color: #53f17b;
}

.step.active {
    background-color: #ecebb6;
    border: 2px solid #007bff;
}

.step-icon {
    font-size: 50px;
    color: #070505;
}

.step-title {
    font-size: 0.95em;
    font-weight: bold;
    margin-bottom: 10px;
    color: #070505;
}

.step-description {
    font-size: 1em;
    color: #070505;
    text-align: left;
}

.step-content {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 10px;
    padding: 20px;
    width: 80%;
}

.navigation-buttons {
    display: flex;
    justify-content: space-between;
    width: 50%;
}

.navigation-buttons button {
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    padding: 10px 20px;
    margin: 5px;
    font-size: 1em;
}

.navigation-buttons button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
}

.step-line {
    position: absolute;
    left: calc(9% - 10px); /* Adjust based on step width */
    height: 2px;
    background-color: #007bff;
    top: 50%;
    transform: translateY(-1px);
    z-index: -1;
}

.button-34 {
    background: #5E5DF0;
    border-radius: 999px;
    box-shadow: #5E5DF0 0 10px 20px -10px;
    box-sizing: border-box;
    color: #FFFFFF;
    cursor: pointer;
    font-family: Inter, Helvetica, "Apple Color Emoji", "Segoe UI Emoji", NotoColorEmoji, "Noto Color Emoji", "Segoe UI Symbol", "Android Emoji", EmojiSymbols, -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    opacity: 1;
    outline: 0 solid transparent;
    padding: 8px 18px;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: fit-content;
    word-break: break-word;
    border: 0;
    text-decoration: none;
}
.button-34 a {
    text-decoration: none;
    color: whitesmoke;
}

@media (max-width: 1200px) {
    .step {
        width: calc(18% - 20px);
    }
    .navigation-buttons {
        width: 60%;
    }
}

@media (max-width: 768px) {
    .step {
        width: calc(28% - 20px);
    }
    .navigation-buttons {
        width: 80%;
    }
}

@media (max-width: 480px) {
    .step {
        width: calc(38% - 20px);
    }
    .navigation-buttons {
        width: 100%;
        flex-direction: column;
    }
    .navigation-buttons button {
        width: 100%;
        margin: 5px 0;
    }
    .step-content {
        width: 90%;
    }
}

@media (max-width: 320px) {
    .step {
        width: calc(100% - 20px);
    }
    .step-content {
        width: 100%;
    }
}
